/* Magnific Popup CSS */

.mfp-bg {
	background: #0B0B0B;
	filter: alpha(opacity=80);
	height: 100%;
	left: 0;
	opacity: 0.8;
	overflow: hidden;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 1042;
}

.mfp-wrap {
	height: 100%;
	left: 0;
	outline: none !important;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 1043;
	-webkit-backface-visibility: hidden;
}

.mfp-container {
	box-sizing: border-box;
	height: 100%;
	left: 0;
	padding: 0 8px;
	position: absolute;
	text-align: center;
	top: 0;
	width: 100%;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

.mfp-container:before {
	content: '';
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}

.mfp-align-top .mfp-container:before {
	display: none;
}

.mfp-content {
	display: inline-block;
	margin: 0 auto;
	position: relative;
	text-align: left;
	vertical-align: middle;
	z-index: 1045;
}

.mfp-inline-holder .mfp-content, .mfp-ajax-holder .mfp-content {
	cursor: auto;
	width: 100%;
}

.mfp-ajax-cur {
	cursor: progress;
}

.mfp-zoom-out-cur, .mfp-zoom-out-cur .mfp-image-holder .mfp-close {
	cursor: zoom-out;
}

.mfp-zoom {
	cursor: zoom-in;
}

.mfp-auto-cursor .mfp-content {
	cursor: auto;
}

.mfp-close, .mfp-arrow, .mfp-preloader, .mfp-counter {
	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;
}

.mfp-loading.mfp-figure {
	display: none;
}

.mfp-hide {
	display: none !important;
}

.mfp-preloader {
	color: #CCC;
	left: 8px;
	margin-top: -0.8em;
	position: absolute;
	right: 8px;
	text-align: center;
	top: 50%;
	width: auto;
	z-index: 1044;
}

.mfp-preloader a {
	color: #CCC;
}

.mfp-preloader a:hover {
	color: white;
}

.mfp-s-ready .mfp-preloader {
	display: none;
}

.mfp-s-error .mfp-content {
	display: none;
}

button.mfp-close, button.mfp-arrow {
	background: transparent;
	border: 0;
	box-shadow: none;
	cursor: pointer;
	display: block;
	outline: none;
	overflow: visible;
	padding: 0;
	z-index: 1046;
	-webkit-appearance: none;
	-webkit-box-shadow: none;
}

button::-moz-focus-inner {
	border: 0;
	padding: 0;
}

.mfp-close {
	color: white;
	filter: alpha(opacity=65);
	font-family: Arial,Baskerville,monospace;
	font-size: 28px;
	font-style: normal;
	height: 44px;
	line-height: 44px;
	opacity: 0.65;
	padding: 0 0 18px 10px;
	position: absolute;
	right: 0;
	text-align: center;
	text-decoration: none;
	top: 0;
	width: 44px;
}

.mfp-close:hover, .mfp-close:focus {
	filter: alpha(opacity=100);
	opacity: 1;
}

.mfp-close:active {
	top: 1px;
}

.mfp-close-btn-in .mfp-close {
	color: #ad3939;
}

.mfp-image-holder .mfp-close, .mfp-iframe-holder .mfp-close {
	color: white;
	padding-right: 6px;
	right: -6px;
	text-align: right;
	width: 100%;
}

.mfp-counter {
	font-size: 12px;
	line-height: 18px;
	right: 0;
	top: 0;
}

.mfp-arrow {
	filter: alpha(opacity=65);
	height: 110px;
	margin: 0;
	margin-top: -55px;
	opacity: 0.65;
	padding: 0;
	position: absolute;
	top: 50%;
	width: 90px;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}

.mfp-arrow:active {
	margin-top: -54px;
}

.mfp-arrow:hover, .mfp-arrow:focus {
	filter: alpha(opacity=100);
	opacity: 1;
}

.mfp-arrow:before, .mfp-arrow:after, .mfp-arrow .mfp-b, .mfp-arrow .mfp-a {
	border: medium inset transparent;
	content: '';
	display: block;
	height: 0;
	left: 0;
	margin-left: 35px;
	margin-top: 35px;
	position: absolute;
	top: 0;
	width: 0;
}

.mfp-arrow:after, .mfp-arrow .mfp-a {
	border-bottom-width: 13px;
	border-top-width: 13px;
	top: 8px;
}

.mfp-arrow:before, .mfp-arrow .mfp-b {
	border-bottom-width: 21px;
	border-top-width: 21px;
	opacity: 0.7;
}

.mfp-arrow-left {
	left: 0;
}

.mfp-arrow-left:after, .mfp-arrow-left .mfp-a {
	border-right: 17px solid white;
	margin-left: 31px;
}

.mfp-arrow-left:before, .mfp-arrow-left .mfp-b {
	border-right: 27px solid #3F3F3F;
	margin-left: 25px;
}

.mfp-arrow-right {
	right: 0;
}

.mfp-arrow-right:after, .mfp-arrow-right .mfp-a {
	border-left: 17px solid white;
	margin-left: 39px;
}

.mfp-arrow-right:before, .mfp-arrow-right .mfp-b {
	border-left: 27px solid #3F3F3F;
}

.mfp-iframe-holder {
	padding-bottom: 40px;
	padding-top: 40px;
}

.mfp-iframe-holder .mfp-content {
	line-height: 0;
	max-width: 900px;
	width: 100%;
}

.mfp-iframe-holder .mfp-close {
	top: -40px;
}

.mfp-iframe-scaler {
	height: 0;
	overflow: hidden;
	padding-top: 56.25%;
	width: 100%;
}

.mfp-iframe-scaler iframe {
	background: black;
	box-shadow: 0 0 8px rgba(0,0,0,0.6);
	display: block;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

/* Main image in popup */

img.mfp-img {
	box-sizing: border-box;
	display: block;
	height: auto;
	line-height: 0;
	margin: 0 auto;
	max-width: 100%;
	padding: 40px 0 40px;
	width: auto;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

/* The shadow behind the image */

.mfp-figure {
	line-height: 0;
}

.mfp-figure:after {
	background: #444;
	bottom: 40px;
	box-shadow: 0 0 8px rgba(0,0,0,0.6);
	content: '';
	display: block;
	height: auto;
	left: 0;
	position: absolute;
	right: 0;
	top: 40px;
	width: auto;
	z-index: -1;
}

.mfp-figure small {
	color: #BDBDBD;
	display: block;
	font-size: 12px;
	line-height: 14px;
}

.mfp-figure figure {
	margin: 0;
}

.mfp-bottom-bar {
	cursor: auto;
	left: 0;
	/*margin-top: -36px;*/
	top: 100%;
	width: 100%;
	font-size: 1.2em;
}

.mfp-title {
	color: #31455d;
	line-height: 18px;
	padding-right: 36px;
	text-align: left;
	word-wrap: break-word;
	font-size: 1.5rem;
	font-weight: 600;
	margin-top: 10px;
}

.mfp-image-holder .mfp-content {
	max-width: 100%;
}

.mfp-gallery .mfp-image-holder .mfp-figure {
	cursor: pointer;
}

.mfp-ie7 .mfp-img {
	padding: 0;
}

.mfp-ie7 .mfp-bottom-bar {
	left: 50%;
	margin-left: -300px;
	margin-top: 5px;
	padding-bottom: 5px;
	width: 600px;
}

.mfp-ie7 .mfp-container {
	padding: 0;
}

.mfp-ie7 .mfp-content {
	padding-top: 44px;
}

.mfp-ie7 .mfp-close {
	padding-top: 0;
	right: 0;
	top: 0;
}

@media screen and (max-width: 800px) and (orientation: landscape), screen and (max-height: 300px) {   /**   * Remove all paddings around the image on small screen   */   .mfp-img-mobile .mfp-image-holder {   padding-left: 0;   padding-right: 0; }   .mfp-img-mobile img.mfp-img {   padding: 0; }   .mfp-img-mobile .mfp-figure:after {   top: 0;   bottom: 0; }   .mfp-img-mobile .mfp-figure small {   display: inline;   margin-left: 5px; }   .mfp-img-mobile .mfp-bottom-bar {   background: rgba(0, 0, 0, 0.6);   bottom: 0;   margin: 0;   top: auto;   padding: 3px 5px;   position: fixed;   -webkit-box-sizing: border-box;   -moz-box-sizing: border-box;   box-sizing: border-box; }   .mfp-img-mobile .mfp-bottom-bar:empty {   padding: 0; }   .mfp-img-mobile .mfp-counter {   right: 5px;   top: 3px; }   .mfp-img-mobile .mfp-close {   top: 0;   right: 0;   width: 35px;   height: 35px;   line-height: 35px;   background: rgba(0, 0, 0, 0.6);   position: fixed;   text-align: center;   padding: 0; } }
@media all and (max-width: 900px) {   
	.mfp-arrow {  -webkit-transform: scale(0.75);   transform: scale(0.75); }   
		.mfp-arrow-left {   -webkit-transform-origin: 0;   transform-origin: 0; }   
		.mfp-arrow-right {   -webkit-transform-origin: 100%;   transform-origin: 100%; }   
		.mfp-container {   padding-left: 6px;   padding-right: 6px; } 
	}